import { useQuery } from "@apollo/client";
import { Backdrop, List } from "@mui/material";
import { useMemo } from "react";
import { ALL_WORKERS } from "../../queries/queries";
import { WorkerListItem } from "../workerListItem/workerListItem";
import { useTranslation } from "react-i18next";

import "./workerView.scss";

const WorkerView = () => {
  const { loading, data } = useQuery(ALL_WORKERS);

  const renderedWorkers = useMemo(
    () =>
      data?.workers.map((worker) => {
        return (
          <WorkerListItem
            name={worker?.name || ""}
            email={worker?.email || ""}
            tel={worker?.phoneNumber || ""}
          />
        );
      }),
    [data]
  );

  const { t } = useTranslation();

  return (
    <div className="worker-view-container">
      <div className="worker-view-toolbar">{t("workers")}</div>
      <div
        style={{ overflow: loading ? "hidden" : "scroll" }}
        className="worker-view-list-items-wrapper"
      >
        <List>{renderedWorkers}</List>
        <Backdrop className="worker-view-backdrop" open={loading}>
          <span className="loader"></span>
        </Backdrop>
      </div>
    </div>
  );
};

export default WorkerView;
