import leftLogo from "../../assets/left-login-screen.svg";
import rightLogo from "../../assets/right-logo.svg";
import { useEffect, useState } from "react";
import { getIsUserLoggedIn, loginUser } from "../../services/appoloService";
import { Navigate, useNavigate } from "react-router-dom";
import useMediaQuery from "@mui/material/useMediaQuery";
import LoginForm from "../loginForm/loginForm";
import "./loginScreen.scss";

const LoginScreen = () => {
  const [successLogin, setSuccessLogin] = useState(false);

  const navigate = useNavigate();
  useEffect(() => {
    if (getIsUserLoggedIn()) {
      navigate("/");
    }
  }, [navigate]);

  const matches = useMediaQuery("(min-width:1000px)");

  return (
    <div className={`login-screen ${!matches && "small-screen"}`}>
      {successLogin && <Navigate to="/constructions" />}
      {matches && (
        <img alt="logo" className="login-screen-left-image" src={leftLogo} />
      )}
      <LoginForm
        onLoginClick={async (email, password) => {
          const user = await loginUser(email, password);
          if (user) {
            setSuccessLogin(true);
          }
        }}
      />
      {matches && (
        <img
          alt="right-logo"
          className="login-screen-right-image"
          src={rightLogo}
        />
      )}
    </div>
  );
};

export default LoginScreen;
