import { useQuery } from "@apollo/client";
import { Backdrop, List } from "@mui/material";
import { useMemo } from "react";
import { useTranslation } from "react-i18next";
import { ALL_MACHINES } from "../../queries/queries";
import { MachineListItem } from "../machineListItem/machineListItem";

import "./machinesView.scss";

const MachinesView = () => {
  const { loading, data } = useQuery(ALL_MACHINES);

  const renderedMachines = useMemo(
    () =>
      data?.machines.map((machine) => {
        return (
          <MachineListItem
            name={machine?.name || ""}
            unitOfMeasure={machine?.unitOfMeasure || ""}
          />
        );
      }),
    [data]
  );

  const { t } = useTranslation();

  return (
    <div className="machines-view-container">
      <div className="machines-view-toolbar">{t("machines")}</div>
      <div
        style={{ overflow: loading ? "hidden" : "scroll" }}
        className="machines-view-list-items-wrapper"
      >
        <List>{renderedMachines}</List>
        <Backdrop className="machines-view-backdrop" open={loading}>
          <span className="loader"></span>
        </Backdrop>
      </div>
    </div>
  );
};

export default MachinesView;
