import { TextField, Button } from "@mui/material";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import buildo from "../../assets/buildo-login.svg";
import PasswordInput from "../passwordInput/passwordInput";
import "./loginForm.scss";

interface LoginFormProps {
  onLoginClick: (email: string, passowrd: string) => void;
}

const LoginForm = ({ onLoginClick }: LoginFormProps) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const { t } = useTranslation();

  return (
    <form>
      <div className="login-form">
        <img className="login-form-logo" alt="buildo" src={buildo} />
        <span className="login-form-web">web.</span>
        <span className="login-form-prompt">{t("enter login details")}</span>
        <div className="login-form-inputs">
          <TextField
            value={email}
            onChange={(event) => setEmail(event.target.value)}
            label="E-mail"
            variant="outlined"
          />
          <PasswordInput label={t("password")} onChange={setPassword} />
          <Button
            variant="contained"
            type="submit"
            onClick={() => onLoginClick(email, password)}
          >
            {t("log in")}
          </Button>
        </div>
      </div>
    </form>
  );
};

export default LoginForm;
