import HouseIcon from "@mui/icons-material/House";
import PeopleIcon from "@mui/icons-material/People";
import BuildIcon from "@mui/icons-material/Build";
// import BusinessIcon from "@mui/icons-material/Business";
import SettingsIcon from "@mui/icons-material/Settings";
import LogoutIcon from "@mui/icons-material/Logout";
import DrawerMenuItem from "../draverMenuItem/drawerMenuItem";
import DrawerMenu from "../drawerMenu/drawerMenu";
import { useEffect, useState } from "react";
import {
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
} from "@mui/material";
import { getIsUserLoggedIn, logoutUser } from "../../services/appoloService";
import { useLocation, useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";

const drawerMenuItems: [string, JSX.Element][] = [
  ["constructions", <HouseIcon />],
  ["workers", <PeopleIcon />],
  ["machines", <BuildIcon />],
  // ["Údaje o firme", <BusinessIcon />, "organization"],
  ["settings", <SettingsIcon />],
];

const LeftMenu = () => {
  const location = useLocation();
  const [selectedMenuItem, setSelectedMenuItem] = useState(
    drawerMenuItems.findIndex(
      (item) => item[0] === location.pathname.substring(1)
    )
  );

  useEffect(() => {
    setSelectedMenuItem(
      drawerMenuItems.findIndex(
        (item) => item[0] === location.pathname.substring(1)
      )
    );
  }, [location]);

  const navigate = useNavigate();
  useEffect(() => {
    if (!getIsUserLoggedIn()) {
      navigate("/login");
    }
  }, [navigate]);

  const renderedItems = drawerMenuItems.map((item, index) => (
    <DrawerMenuItem
      selected={index === selectedMenuItem}
      icon={item[1]}
      text={item[0]}
      href={item[0]}
    />
  ));
  const { t } = useTranslation();

  renderedItems.push(
    <ListItem disablePadding>
      <ListItemButton
        onClick={async () => {
          await logoutUser();
          navigate("/login");
        }}
      >
        <ListItemIcon>
          <LogoutIcon />
        </ListItemIcon>
        <ListItemText primary={t("logout")} />
      </ListItemButton>
    </ListItem>
  );

  return <DrawerMenu>{renderedItems}</DrawerMenu>;
};

export default LeftMenu;
